.logo-container--haiku-generator {
  min-width: 105px;
  max-width: 105px; }

.cls-haiku-generator {
  fill: grey; }

.app-body__block:hover .feather-blink {
  -webkit-animation: feather-blink 1s infinite;
          animation: feather-blink 1s infinite; }

@-webkit-keyframes feather-blink {
  0% {
    opacity: 1; }
  33% {
    opacity: 0.6; }
  33% {
    opacity: 0.6; }
  100% {
    opacity: 1; } }

@keyframes feather-blink {
  0% {
    opacity: 1; }
  33% {
    opacity: 0.6; }
  33% {
    opacity: 0.6; }
  100% {
    opacity: 1; } }



